 body {
   /* background-color: #101016; */
   /* overflow: hidden; */
   height: 100vh;
 }


 .submitbutton {
   position: relative;
   /* top: 0.7cm; */
   display: block;
   /* margin: 1rem 0; */
   /* padding: 0.35rem 0.5rem; */
   background-color: blueviolet;
   color: white;
   border: none;
   border-radius: 3px;
   width: 50%;
   font-size: 1rem;
   font-weight: bold;
   cursor: pointer;
   margin: 0;
   margin-top: 25px;
   height: 1cm;
   margin-bottom: 15px;
   left: 6rem;
 }

 input.input-error,
 select.input-error {
   border-color: #fc8181;
 }

 button:disabled {
   opacity: 0.35;
 }


 .signuperror {
   color: #fc8181;
   font-size: 1rem;
   text-align: left;
   margin: 0;
   margin-top: 0.25rem;
 }


 .signup {
   position: relative;
   width: 80%;
   top: 0.0cm;
   /* background-color: burlywood; */
   /* width: 90%; */
   height: fit-content;
   font-size: 14px;
   /* max-width: 270px; */
   margin: 0;
   margin-left: 2rem;
   /* margin-right: 2cm; */
   /* background-color: aqua; */
 }

 /* form label {
   /* font-size: 1.3rem; */
 /* font-weight: bold;
   display: block;
   text-align: left;
   margin: 0.3rem 0 0.2rem;
 } */
 .signuplabel {
   color: black;
   font-size: 0.8rem;
   margin-left: 2rem;
   margin-bottom: 10px;
 }

 input {
   width: 100%;
   height: 10px;
   margin-bottom: 10px;
   padding: 0.65rem 0.5rem;
   /* font-size: 20px; */
   color: black;
   border: 2px solid #050505;
   background: transparent;
   border-radius: 10px;
   outline: none;
 }

 ::-webkit-input-placeholder {
   font-size: 1rem;
   color: rgb(129, 127, 127);
 }

 .formcontainer {
   margin-top: 2cm;
   width: 25rem;
   height: fit-content;
   border-radius: 15px;
   border: 1px solid gray;
   /* box-shadow: 6px 6px 10px 15px rgba(145, 83, 145, 0.2); */
   /* background-color: rgb(39, 38, 38); */
   margin-left: 12cm;
   align-items: center;
   justify-content: center;
 }

 .signup-heading {
   font-size: 35px;
   font-weight: 600;
   color: black;
   font-family: 'Poppins', sans-serif;
   position: relative;
   margin-top: 0.2cm;
   margin-left: 4cm;
 }


